.media-player {
  z-index: 100;
  position: fixed;
  top: 0;

  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;

  &__content {
    position: relative;
  }

  &__video {
    min-width: 300px;
    width: 90vw;
    aspect-ratio: 16 / 9;
    height: auto;

    @include on-small-desktop {
      height: 65vh;
      aspect-ratio: 16 / 9;
      width: auto;
    }
  }

  &__close-btn {
    position: absolute;
    top: 20px;
    right: 15px;
  }
}
