.page {
  scroll-behavior: smooth;
  box-sizing: border-box;
  transition: all .3s;
  position: relative;
  overflow-x: hidden;
}

.page:has(body aside[class*="--active"]) {
  overflow-y: hidden;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__aside {
  opacity: 0;
  pointer-events: none;
  background-color: #0000;
  transition: opacity .3s, background-color .3s, transform .3s .3s;
  transform: translate(-120%);
}

.page__aside--active {
  pointer-events: all;
  opacity: 1;
  background-color: #000000b3;
  transition: transform .3s, background-color .3s .3s;
  transform: translateX(0);
}

.page__menu {
  z-index: 2;
  pointer-events: none;
  transition: all .5s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:before {
  content: "";
  z-index: -1;
  pointer-events: none;
  background-color: #000000b3;
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

