.page {
  scroll-behavior: smooth;
  transition: all 0.3s;
  overflow-x: hidden;
  box-sizing: border-box;
  position: relative;
}
.page:has(body aside[class*='--active']) {
  overflow-y: hidden;
}
.page:has(.page__menu:target) {
  overflow: hidden;
}
.page__aside {
  opacity: 0;
  pointer-events: none;
  transform: translate(-120%);
  background-color: transparent;
  transition:
    opacity 0.3s,
    background-color 0.3s,
    transform 0.3s 0.3s;
}
.page__aside--active {
  pointer-events: all;
  opacity: 1;
  background-color: rgba(0, 0, 0, 0.7019607843);
  transform: translateX(0);
  transition:
    transform 0.3s,
    background-color 0.3s 0.3s;
}
.page__menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
  transition: all 0.5s;
  transform: translateX(-100%);
  pointer-events: none;
}
.page__menu::before {
  content: '';
  z-index: -1;
  background-color: rgba(0, 0, 0, 0.7019607843);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  pointer-events: none;
}
.page__menu:target {
  opacity: 1;
  transform: translateX(0);
  pointer-events: all;
}

